  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  color: #333;
  font-family: "Mulish", sans-serif;
}

h1, h2, h3, h4 {
  margin-bottom: 2rem;
}

h1 {
  font-size: 3.5rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.5rem;
}

h4 {
  font-size: 2rem;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
}

a {
  color: #333;
  text-decoration: none;
}

.container {
  min-height: 100vh;
  display: grid;
  grid: "header" auto "content" 1fr "footer" auto/auto;
}

header {
  grid-area: header;
  padding: 3rem;
  background-color: #2ecc71;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
header a {
  font-size: 4rem;
  color: white;
}
header .header-brand {
  font-size: 4rem;
  font-weight: 700;
}
header ul {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
header ul li .header-nav {
  font-size: 1.8rem;
  padding: 0 10px;
}

.categories .active {
  font-weight: 700;
  color: #2ecc71;
}

.content {
  grid-area: content;
  padding: 2rem;
}

footer {
  grid-area: footer;
  color: white;
  background: #333;
  padding: 2rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.text-error {
  color: rgb(137, 46, 46);
}

.btn {
  border: none;
  border-radius: 5px;
  padding: 1rem 2rem;
  font-weight: 700;
  cursor: pointer;
}
.btn:hover {
  box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.3), 0 1px 3px 1px rgba(60, 64, 67, 0.15);
  transition: box-shadow 0.3s;
}
.btn-primary {
  background-color: #2ecc71;
  color: white;
}
.btn-secondary {
  border: 1px solid #2ecc71;
  background: white;
  color: #2ecc71;
}
.btn-danger {
  background-color: rgb(137, 46, 46);
  color: white;
}

.selected {
  color: #2ecc71;
  font-weight: 700;
}

.calc {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.calc .modal {
  width: 30%;
  background-color: white;
  border: 1px solid #333;
  border-radius: 0.3rem;
  padding: 40px;
  box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.3), 0 1px 3px 1px rgba(60, 64, 67, 0.15);
  text-align: center;
}
.calc .modal p {
  font-size: 2rem;
}
.calc .modal .btn {
  margin-top: 30px;
  margin-right: 10px;
}`, "",{"version":3,"sources":["webpack://./src/assets/styles/_reset.scss","webpack://./src/assets/styles/style.scss","webpack://./src/assets/styles/_base.scss","webpack://./src/assets/styles/_variables.scss","webpack://./src/assets/styles/_classes.scss","webpack://./src/assets/styles/_mixins.scss"],"names":[],"mappings":"AAAA;EACI,SAAA;EACA,UAAA;EACA,sBAAA;ACEJ;;ACLA;EACI,gBAAA;ADQJ;;ACLA;EACI,iBAAA;EACA,WCDS;EDET,iCCCU;AFOd;;ACLA;EACI,mBAAA;ADQJ;;ACFI;EACI,iBAJQ;ADShB;;ACNI;EACI,eAJQ;ADahB;;ACVI;EACI,iBAJQ;ADiBhB;;ACdI;EACI,eAJQ;ADqBhB;;ACbA;EACI,gBAAA;ADgBJ;;ACbA;EACI,eAAA;ADgBJ;;ACbA;EACI,WC1BS;ED2BT,qBAAA;ADgBJ;;AG/CA;EACI,iBAAA;EACA,aAAA;EACA,oDACI;AHiDR;;AG3CA;EACI,iBAAA;EACA,aAAA;EACA,yBDZY;EEDZ,aAAA;EACA,mBAFoB;EAGpB,8BDawB;ECZxB,mBAJmD;AJgEvD;AG9CI;EACI,eAAA;EACA,YAAA;AHgDR;AG7CI;EACI,eAAA;EACA,gBAAA;AH+CR;AG5CI;EC3BA,aAAA;EACA,mBAFoB;EAGpB,uBAHmC;EAInC,mBAJmD;AJ8EvD;AG5CY;EACI,iBAAA;EACA,eAAA;AH8ChB;;AGxCA;EACI,gBAAA;EACA,cD1CY;AFqFhB;;AGxCA;EACI,kBAAA;EACA,aAAA;AH2CJ;;AGvCA;EACI,iBAAA;EACA,YAAA;EACA,gBDnDS;ECoDT,aAAA;ECxDA,aAAA;EACA,mBAFoB;EAGpB,uBAHmC;EAInC,mBAJmD;AJuGvD;;AGzCA;EACI,uBDzDU;AFqGd;;AGzCA;EACI,YAAA;EACA,kBAAA;EACA,kBAAA;EACA,gBAAA;EACA,eAAA;AH4CJ;AG1CI;EACI,mFDjEK;ECkEL,2BAAA;AH4CR;AGzCI;EACI,yBD7EQ;EC8ER,YAAA;AH2CR;AGxCI;EACI,yBAAA;EACA,iBAAA;EACA,cDpFQ;AF8HhB;AGvCI;EACI,kCDpFM;ECqFN,YAAA;AHyCR;;AGrCA;EACI,cD9FY;EC+FZ,gBAAA;AHwCJ;;AGpCA;EACI,eAAA;EACA,MAAA;EACA,OAAA;EACA,WAAA;EACA,aAAA;EACA,oCAAA;EC1GA,aAAA;EACA,mBAFoB;EAGpB,uBAHmC;EAInC,mBAJmD;AJsJvD;AGvCI;EACI,UAAA;EACA,uBAAA;EACA,sBAAA;EACA,qBAAA;EACA,aAAA;EACA,mFD5GK;EC6GL,kBAAA;AHyCR;AGvCQ;EACI,eAAA;AHyCZ;AGtCQ;EACI,gBAAA;EACA,kBAAA;AHwCZ","sourcesContent":["* {\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n}","@import url(\"https://fonts.googleapis.com/css2?family=Mulish:wght@200;300;400;500;600;700;800;900&display=swap\");\n* {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n}\n\n:root {\n  font-size: 62.5%;\n}\n\nbody {\n  font-size: 1.6rem;\n  co